import React, { useState } from "react";
import {
  Box,
  Typography,
  IconButton,
  Badge,
  Divider,
  Menu,
  MenuItem,
  Container,
  Grid,
  Link,
  Modal,
  Button,
  TextField,
  Checkbox,
  FormControlLabel,
} from "@mui/material";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import {
  SyncAlt,
  ExpandMore,
  Favorite,
  ShoppingBag,
  Star,
  Spa,
} from "@mui/icons-material";
import MenuIcon from "@mui/icons-material/Menu";
import { MdKeyboardArrowRight } from "react-icons/md";

// Login Validation Schema using Yup
const LoginSchema = Yup.object().shape({
  email: Yup.string().email("Invalid email").required("Required"),
  password: Yup.string().min(6, "Too Short!").required("Required"),
  rememberMe: Yup.boolean(),
});

// SignUp Validation Schema using Yup
const SignupSchema = Yup.object().shape({
  firstName: Yup.string().required("First name is required"),
  lastName: Yup.string().required("Last name is required"),
  email: Yup.string()
    .email("Invalid email format")
    .required("Email is required"),
  phone: Yup.string().required("Phone is required"),
  password: Yup.string()
    .min(8, "Password must be atleast 8 characters")
    .required("Password is required"),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref("password")], "Passwords must match")
    .required("Confirm password is required"),
  terms: Yup.bool().oneOf([true], "You must agree with terms and conditions"),
});

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "auto",
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
};

const style2 = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "auto",
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
};

const NavBar: React.FC = () => {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose1 = () => setOpen(false);

  const [open2, setOpen2] = React.useState(false);
  const handleOpen2 = () => setOpen2(true);
  const handleClose2 = () => setOpen2(false);

  // State for each dropdown
  const [categoryAnchorEl, setCategoryAnchorEl] = useState<null | HTMLElement>(
    null
  );
  const [offersAnchorEl, setOffersAnchorrEl] = useState<null | HTMLElement>(
    null
  );
  const [storesAnchorEl, setStoresAnachorEl] = useState<null | HTMLElement>(
    null
  );
  const [brandsAnchorEl, setBrandsAnchorEl] = useState<null | HTMLElement>(
    null
  );

  // Initial form values
  const initialValues = {
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    password: "",
    confirmPassword: "",
    referCode: "",
    terms: false,
  };

  // Handlers for dropdown toggles
  const handleCategoryClick = (event: React.MouseEvent<HTMLElement>) => {
    setCategoryAnchorEl(event.currentTarget);
  };

  const handleOffersClick = (event: React.MouseEvent<HTMLElement>) => {
    setOffersAnchorrEl(event.currentTarget);
  };

  const handleStoresClick = (event: React.MouseEvent<HTMLElement>) => {
    setStoresAnachorEl(event.currentTarget);
  };

  const handleBrandsClick = (event: React.MouseEvent<HTMLElement>) => {
    setBrandsAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setCategoryAnchorEl(null);
    setOffersAnchorrEl(null);
    setStoresAnachorEl(null);
    setBrandsAnchorEl(null);
  };

  return (
    <Box className="bg-white shadow-md">
      <Container maxWidth={"xl"}>
        <Box className="flex flex-wrap items-center justify-between py-2 bg-white">
          {/* Left Section - Category Dropdown */}
          <Box className="flex items-center bg-[#00670c] p-2 w-full md:w-auto">
            <IconButton onClick={handleCategoryClick}>
              <MenuIcon className="text-white" />
            </IconButton>
            <Typography
              onClick={handleCategoryClick}
              className="text-white ml-2 cursor-pointer"
            >
              Select Category
            </Typography>
            <IconButton onClick={handleCategoryClick}>
              <ExpandMore className="text-white" />
            </IconButton>

            {/* Select Category Dropdown */}
            <Menu
              anchorEl={categoryAnchorEl}
              open={Boolean(categoryAnchorEl)}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose}>Category 1</MenuItem>
              <MenuItem onClick={handleClose}>Category 2</MenuItem>
              <MenuItem onClick={handleClose}>Category 3</MenuItem>
            </Menu>
          </Box>

          {/* Middle Section - Navigation Links */}
          <Box className="flex flex-wrap items-center justify-center space-x-4 mt-2 md:mt-0 w-full md:w-auto">
            <Typography component="a" href="#" className="text-gray-700">
              HOME
            </Typography>

            {/* Offers Dropdown */}
            <Typography
              component="a"
              href="#"
              className="text-gray-700 flex items-center"
              onClick={handleOffersClick}
            >
              OFFERS
              <ExpandMore className="ml-1" fontSize="small" />
            </Typography>
            <Menu
              anchorEl={offersAnchorEl}
              open={Boolean(offersAnchorEl)}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose}>Flash Deals</MenuItem>
            </Menu>

            {/* Stores Dropdown */}
            <Typography
              component="a"
              href="#"
              className="text-gray-700 flex items-center"
              onClick={handleStoresClick}
            >
              STORES
              <ExpandMore className="ml-1" fontSize="small" />
            </Typography>
            <Menu
              anchorEl={storesAnchorEl}
              className="right-[250px]"
              open={Boolean(storesAnchorEl)}
              onClose={handleClose}
            >
              <Grid container maxHeight={"xs"} spacing={3}>
                <Grid item xs={8}>
                  <Grid container spacing={3} className="p-3">
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link
                        href={"#"}
                        className="text-[#00670c] ms-5 mt-9 flex items-center gap-2 "
                      >
                        View All <MdKeyboardArrowRight />
                      </Link>
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={4}>
                  <Box>
                    <img
                      src="/dropdown-img.png"
                      className="w-full h-full"
                      alt="image"
                    />
                  </Box>
                </Grid>
              </Grid>
            </Menu>

            {/* Brands Dropdown */}
            <Typography
              component="a"
              href="#"
              className="text-gray-700 flex items-center"
              onClick={handleBrandsClick}
            >
              BRANDS
              <ExpandMore className="ml-1" fontSize="small" />
            </Typography>
            <Menu
              anchorEl={brandsAnchorEl}
              open={Boolean(brandsAnchorEl)}
              onClose={handleClose}
              className="right-[250px]"
            >
              <Grid container spacing={3}>
                <Grid item xs={12} className="mx-auto">
                  <Grid container spacing={3} className="p-3">
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link href={"#"} className="flex gap-3 items-center">
                        <img
                          src="/logo 1.png"
                          className="w-[90px] h-[90px]"
                          alt="shop"
                        />
                        <p>Royal Garden</p>
                      </Link>
                    </Grid>
                    <Grid item xs={6} className="!pt-0">
                      <Link
                        href={"#"}
                        className="text-[#00670c] ms-5 mt-9 flex items-center gap-2 "
                      >
                        View All <MdKeyboardArrowRight />
                      </Link>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Menu>

            {/* Discounted Products */}
            <Typography
              component="a"
              href="#"
              className="text-gray-700 flex items-center"
            >
              DISCOUNTED PRODUCTS
              <Star className="text-yellow-500 ml-1" fontSize="small" />
            </Typography>
          </Box>

          {/* Right Section - Icons and User Actions */}
          <Box className="flex items-center space-x-4 mt-2 md:mt-0 w-full md:w-auto justify-center">
            <Typography
              onClick={handleOpen}
              component="a"
              href="#"
              className="bg-[#40bf00] px-4 py-2 text-white rounded"
            >
              Login / Register
            </Typography>
            <Divider
              orientation="vertical"
              flexItem
              className="border-gray-300"
            />
            <IconButton className="text-gray-700">
              <SyncAlt />
            </IconButton>
            <IconButton className="text-gray-700">
              <Favorite />
            </IconButton>
            <Divider
              orientation="vertical"
              flexItem
              className="border-gray-300"
            />
            <Box className="relative">
              <IconButton className="text-gray-700">
                <Badge badgeContent={0} color="primary">
                  <ShoppingBag />
                </Badge>
              </IconButton>
            </Box>
          </Box>
        </Box>
      </Container>
      <Modal
        open={open}
        onClose={handleClose1}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <div className="flex item-center justify-center">
            <Box className="rounded-lg w-full">
              <div className="flex flex-col items-center mb-6">
                <Box className="flex items-center gap-4">
                  <Box className="bg-[#00670c] p-2 rounded">
                    <Spa className="text-white text-2xl" />
                  </Box>
                  <Typography
                    variant="h5"
                    className="font-bold text-[#00670c] ml-2"
                  >
                    GoSellar
                  </Typography>
                </Box>
              </div>
              <h2 className="text-2xl font-bold mb-4">Login</h2>
              <p className="text-gray-600 mb-4">
                Login to your account. Don&apos;t have an account?{" "}
                <a
                  href="#"
                  onClick={(e) => {
                    e.preventDefault();
                    handleClose1();
                    handleOpen2();
                  }}
                  className="text-green-600"
                >
                  SignUp
                </a>
              </p>

              <Formik
                initialValues={{
                  email: "",
                  password: "",
                  rememberMe: false,
                }}
                validationSchema={LoginSchema}
                onSubmit={(values) => {
                  console.log(values);
                }}
              >
                {({ errors, touched, handleChange, handleBlur }) => (
                  <Form className="space-y-4">
                    {/* Email Field*/}
                    <Field
                      name="email"
                      as={TextField}
                      label="Email / Phone"
                      fullWidth
                      variant="outlined"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      error={touched.email && Boolean(errors.email)}
                      helperText={touched.email && errors.email}
                      className="w-full"
                    />

                    {/* Password Field */}
                    <Field
                      name="password"
                      as={TextField}
                      label="Password"
                      type="password"
                      fullWidth
                      variant="outlined"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      error={touched.password && Boolean(errors.password)}
                      helperText={touched.password && errors.password}
                      className="w-full"
                    />

                    {/* Remember Me */}
                    <div className="flex justify-between items-center">
                      <FormControlLabel
                        control={<Checkbox name="rememberMe" />}
                        label="Remember Me"
                      />
                      <a href="#" className="text-[#00670c]">
                        Forgot Password?
                      </a>
                    </div>

                    {/* reCAPTCHA placeholder */}
                    {/* <Box className="border rounded-lg p-4">
                      <FormControlLabel
                      control={<Checkbox/>}
                      label="I'm not a robot"/>
                      <div className="mt-2 text-gray-500 text-sm">reCAPTCHA Privacy - Terms</div>
                    </Box> */}

                    {/* Loginn Button */}
                    <Button
                      type="submit"
                      variant="contained"
                      fullWidth
                      className="bg-[#40bf00] text-white hover:bg-[#00670c]"
                    >
                      Login
                    </Button>

                    {/* Alternative Login */}
                    <div className="text-center mt-4 text-gray-600">
                      Or continue with
                    </div>
                    <div className="flex justify-center mt-2">
                      <Button className="bg-white border rounded-full p-2 mx-1">
                        <img
                          src="/google-icon.svg"
                          alt="Google logo"
                          className="w-5 h-5"
                        />
                      </Button>
                      <Button className="bg-white border rounded-full p-2 mx-1">
                        <img
                          src="/facebook-icon.svg"
                          alt="Facebook Logo"
                          className="w-5 h-5"
                        />
                      </Button>
                    </div>
                  </Form>
                )}
              </Formik>
            </Box>
          </div>
        </Box>
      </Modal>
      <Modal
        open={open2}
        onClose={handleClose2}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style2}>
          <div className="flex items-center justify-center">
            <div className="bg-white rounded-lg w-full">
              <Formik
                initialValues={initialValues}
                validationSchema={SignupSchema}
                onSubmit={(values) => {
                  console.log(values);
                  alert("Form Submitted");
                }}
              >
                {({ values, handleChange, handleSubmit }) => (
                  <Form onSubmit={handleSubmit}>
                    <div className="mb-6">
                      <Box className="flex justify-center mb-4 items-center gap-4">
                        <Box className="bg-[#00670c] p-2 rounded">
                          <Spa className="text-white text-2xl" />
                        </Box>
                        <Typography
                          variant="h5"
                          className="font-bold text-[#00670c] ml-2"
                        >
                          GoSellar
                        </Typography>
                      </Box>
                      <h1 className="text-2xl font-bold text-gray-700">Sign Up</h1>
                      <p className="text-gray-600 mb-4 mt-4">
                        Let&apos;s get you started. Already have an account?
                        <a
                          href="#"
                          onClick={(e) => {
                            e.preventDefault();
                            handleClose2();
                            handleOpen();
                          }}
                          className="text-green-600"
                        >
                          Login
                        </a>
                      </p>
                    </div>

                    <Box className="h-[50vh] overflow-scroll overflow-x-hidden">
                      <Grid container spacing={2}>
                        <Grid item xs={6}>
                          <Field
                            as={TextField}
                            name="firstName"
                            label="First Name"
                            variant="outlined"
                            fullWidth
                            className="mb-4"
                            helperText={
                              <ErrorMessage
                                name="firstName"
                                component="div"
                                className="text-red-500 text-sm"
                              />
                            }
                          />
                        </Grid>
                        <Grid item xs={6}>
                          <Field
                            as={TextField}
                            name="lastName"
                            label="Last Name"
                            variant="outlined"
                            fullWidth
                            className="mb-4"
                            helperText={
                              <ErrorMessage
                                name="lastName"
                                component="div"
                                className="text-red-500 text-sm"
                              />
                            }
                          />
                        </Grid>
                      </Grid>

                      <Field
                        as={TextField}
                        name="email"
                        label="Email"
                        variant="outlined"
                        fullWidth
                        className="mb-4"
                        helperText={
                          <ErrorMessage
                            name="email"
                            component="div"
                            className="text-red-500 text-sm"
                          />
                        }
                      />

                      <Field
                        as={TextField}
                        name="phone"
                        label="Phone"
                        variant="outlined"
                        fullWidth
                        className="mb-4"
                        helperText={
                          <ErrorMessage
                            name="phone"
                            component="div"
                            className="text-red-500 text-sm"
                          />
                        }
                      />

                      <Grid container spacing={2}>
                        <Grid item xs={6}>
                          <Field
                            as={TextField}
                            name="password"
                            label="Password"
                            type="password"
                            variant="outlined"
                            fullWidth
                            className="mb-4"
                            helperText={
                              <ErrorMessage
                                name="password"
                                component="div"
                                className="text-red-500 text-sm"
                              />
                            }
                          />
                        </Grid>
                        <Grid item xs={6}>
                          <Field
                            as={TextField}
                            name="confirmPassword"
                            label="Confirm Password"
                            type="password"
                            variant="outlined"
                            fullWidth
                            className="mb-4"
                            helperText={
                              <ErrorMessage
                                name="confirmPassword"
                                component="div"
                                className="text-red-500 text-sm"
                              />
                            }
                          />
                        </Grid>
                      </Grid>

                      <Field
                        as={TextField}
                        name="referCode"
                        label="Refer Code (Optional)"
                        variant="outlined"
                        fullWidth
                        className="mb-4"
                      />

                      <FormControlLabel
                        control={
                          <Checkbox
                            name="terms"
                            checked={values.terms}
                            onChange={handleChange}
                          />
                        }
                        label="I agree to the terms and conditions"
                      />
                      <ErrorMessage
                        name="terms"
                        component="div"
                        className="text-red-500 text-sm"
                      />
                    </Box>

                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      fullWidth
                      className="mt-4 bg-[#40bf00] hover:bg-[#00670c]"
                    >
                      Sign Up
                    </Button>
                    {/* Alternative Login */}
                    <div className="text-center mt-4 text-gray-600">
                      Or continue with
                    </div>
                    <div className="flex justify-center mt-2">
                      <Button className="bg-white border rounded-full p-2 mx-1">
                        <img
                          src="/google-icon.svg"
                          alt="Google logo"
                          className="w-5 h-5"
                        />
                      </Button>
                      <Button className="bg-white border rounded-full p-2 mx-1">
                        <img
                          src="/facebook-icon.svg"
                          alt="Facebook Logo"
                          className="w-5 h-5"
                        />
                      </Button>
                    </div>
                  </Form>
                )}
              </Formik>
            </div>
          </div>
        </Box>
      </Modal>
    </Box>
  );
};

export default NavBar;
